    box-shadow: 5px 5px 10px 5px rgba(0, 0, 0, .1);
    transition: .5s;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    left: -40px;
    z-index: 200;
    position: fixed;
    top:20%
}

#global-container .btn-nav i {
    transition: all .5s ease;
    font-size: 22px;
    line-height: 0;
    position: absolute;
    left: 55%;
    top:50%
}

.docs-page-header {
    font-size: 24px;
    padding: 30px 15px;
    color: #000;
    background: url(https://satellites.maif.fr/maif/composants/img/rwd/section/mood-1.png);
    background-size: cover;
    background-position: center;
    margin-bottom: 40px;
    text-align:center
}

.docs-page-header h1 {
    font-size: 36px;
    color: #000;
    font-family: maif, Arial, sans-serif;
    font-weight: 700;
    display: block;
    text-align:center
}

.docs-page-header p {
    margin:0 0 10px !important
}

h1.page-header {
    font-size: 24px;
    display: block;
    color: #000;
    font-weight: 700;
    padding-bottom: 4px;
    border: none;
    margin:20px 0 10px
}

h1.page-header:after {
    display: block;
    content: "";
    width: 80px;
    height: 3px;
    background: #fc0d1a;
    margin:5px 0 0
}

h1.page-header small {
    font-weight: 400;
    color:#f9545e
}

h1.page-header small:hover {
    color:#fc0d1a
}

